import { Container, Row, Tab, Tabs } from "react-bootstrap";
import React from "react";
import CustomDivider from "./CustomDivider";
import NFTCard from "./NFTCard";
import { getExplore } from "./storage";

function ExploreController() {
  const [newList, setNewList] = React.useState(null);
  const [volumeList, setVolumeList] = React.useState(null);
  //const [nonceList, setNonceList] = React.useState(null);

  React.useEffect(() => {
    document.title = `Explore NFTs`;
    (async () => {
      const byNew = await getExplore("new");
      const byVolume = await getExplore("volume");
      //const byNonce = await getExplore("nonce");

      setNewList(byNew);
      setVolumeList(byVolume);
      //setNonceList(byNonce);
    })();
  }, []);

  return (
    <div>
      <Container className="App-header">
        <Row>
          <h1 className="mt-4">Explore NFTs</h1>
          <CustomDivider />
        </Row>
      </Container>
      <Tabs
        defaultActiveKey="volume"
        className="justify-content-center"
        variant="pills"
      >
        <Tab eventKey="volume" title="Hot NFTS">
          {volumeList !== null && (
            <ul className="cards-container">
              {volumeList.map((x) => (
                <a href={`/nft?address=${x}`}>
                  <NFTCard key={x} address={x} />
                </a>
              ))}
            </ul>
          )}
        </Tab>
        <Tab eventKey="new" title="New Listings">
          {newList !== null && (
            <ul className="cards-container">
              {newList.map((x) => (
                <a href={`/nft?address=${x}`}>
                  <NFTCard key={x} address={x} />
                </a>
              ))}
            </ul>
          )}
        </Tab>
      </Tabs>
    </div>
  );
}

export default ExploreController;
