import {
  Modal,
  Container,
  Row,
  Tab,
  Tabs,
  Button,
  Form,
} from "react-bootstrap";
import React from "react";
import CustomDivider from "./CustomDivider";
import Sha256 from "crypto-js/sha256";
import Hex from "crypto-js/enc-hex";
import { getProfile } from "./storage";
import NFTCard from "./NFTCard";
import { updateProfile } from "./api";

function ProfileController() {
  const [address, setAddress] = React.useState(null);
  const [editShow, setEditShow] = React.useState(null);
  const [name, setName] = React.useState("");
  const nameRef = React.createRef();
  const [profile, setProfile] = React.useState(null);

  React.useEffect(() => {
    setAddress(new URLSearchParams(window.location.search).get("address"));
  }, []);

  React.useEffect(() => {
    if (address !== null) {
      document.title = `Profile | ${address}`;
      (async () => {
        const p = await getProfile(address);
        setProfile(p);
        if (p !== null) {
          if (p.name === null) {
            setName("");
          } else {
            setName(p.name);
          }
        }
      })();
    }
  }, [address]);

  const doSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    const payload = {
      timestamp: Date.now(),
      owner: address,
      name: name === null || name === "" ? null : name,
    };
    const message = JSON.stringify(payload);
    console.log(message);

    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }
    payload.signature = signResult.result;

    const updateResult = await updateProfile(payload);
    if ("error" in updateResult) {
      return;
    }

    window.location.reload(false);
  };

  return (
    <div>
      <Container className="App-header">
        <Row>
          <h1 className="mt-4">
            {profile === null
              ? "-"
              : profile.name === "" || profile.name === null
              ? "Unnamed Profile"
              : profile.name}
          </h1>
          <h6>{address}</h6>
          <p>
            <span>
              <div
                className="clickable"
                style={{ display: "inline" }}
                onClick={() => setEditShow(true)}
              >
                (Edit)
              </div>
            </span>
          </p>
          <CustomDivider />
        </Row>
      </Container>
      <Tabs
        defaultActiveKey={
          profile === null
            ? "owned"
            : profile.createdNfts.length === 0
            ? "owned"
            : "created"
        }
        className="justify-content-center"
        variant="pills"
      >
        <Tab eventKey="created" title="NFT Creations">
          {profile !== null && (
            <ul className="cards-container">
              {profile.createdNfts.map((x) => (
                <a href={`/nft?address=${x}`}>
                  <NFTCard key={x} address={x} />
                </a>
              ))}
            </ul>
          )}
        </Tab>
        <Tab eventKey="owned" title="Owned NFTs">
          {profile !== null && (
            <ul className="cards-container">
              {profile.ownedNfts.map((x) => (
                <a href={`/nft?address=${x}`}>
                  <NFTCard key={x} address={x} />
                </a>
              ))}
            </ul>
          )}
        </Tab>
      </Tabs>

      <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        show={editShow}
        onHide={() => setEditShow(false)}
        onEntered={() => nameRef.current.focus()}
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Edit profile
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={doSubmit}>
            <Form.Group className="mb-3">
              <Form.Label>Profile name (leave blank if no change)</Form.Label>
              <Form.Control
                type="text"
                value={name}
                placeholder="Max 40 characters"
                onChange={(e) => setName(e.target.value)}
                isValid={name !== "" && name.length <= 40}
                isInvalid={name !== "" && name.length > 40}
                ref={nameRef}
              />
              {name.length > 40 && (
                <div>
                  <Form.Text className="input-error">Name too long</Form.Text>
                </div>
              )}
            </Form.Group>

            <div style={{ textAlign: "center" }}>
              <Button
                className="popup-button mb-3 mt-3 px-4"
                variant="primary"
                type="submit"
              >
                Verify and update profile
              </Button>
            </div>
          </Form>
        </Modal.Body>
      </Modal>
    </div>
  );
}

export default ProfileController;
